import React, { useEffect } from 'react';
import styled from 'styled-components';

const Ads = ( float ) => {

  useEffect(() => {
    try {
      const adsbygoogle = window.adsbygoogle || []
      adsbygoogle.push({})
    } catch (e) {
      console.error(e)
    }
  }, [])
  
  return (
    <Content>
    <ins className='adsbygoogle'
        style={float ? { display: 'block', position: '-webkit-sticky', position: 'sticky', top: '10px' } : {}}
        data-ad-client= 'ca-pub-6106226996374193'
        data-ad-slot={4511921079}
        data-ad-format= 'auto'
        data-full-width-responsive="true"
    >
    </ins>
    </Content>
  );
};

export default Ads;

const Content = styled.div`
  position:-webkit-sticky; 
  position:sticky; 
  top: 10px;
  margin: 0 auto;
  width: 200px;
`;